
.animate {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
.animate--grow {
    -webkit-animation-name: grow;
            animation-name: grow;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
}
.animate--top-enter {
    -webkit-animation-name: topEnter;
            animation-name: topEnter;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
}
.animate--top-leave {
    -webkit-animation-name: topLeave;
            animation-name: topLeave;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
}
@-webkit-keyframes grow {
0% {
    opacity: 0;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
100% {
    opacity: 1;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes grow {
0% {
    opacity: 0;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
100% {
    opacity: 1;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes topEnter {
0% {
    opacity: 0;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
}
100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
}
@keyframes topEnter {
0% {
    opacity: 0;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
}
100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
}
@-webkit-keyframes topLeave {
0% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    opacity: 0;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
}
}
@keyframes topLeave {
0% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    opacity: 0;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
}
}
.container {
  max-width: 760px;
  margin: 0 auto;
  padding: 20px;
  width: 100%;
}
.container--no-padding {
    padding: 0;
}
.container--centered {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    min-height: 100%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.button {
  display: inline-block;
  font: inherit;
  font-weight: 800;
  border: 0;
  padding: 15px 30px;
  border-radius: 3px;
  outline: 0;
  cursor: pointer;
  background-color: #eee;
  box-shadow: 0 5px #d0d0d0;
  transition: box-shadow 200ms;
  text-decoration: none;
}
.button:hover {
    top: 2px;
    position: relative;
    box-shadow: 0 3px #d0d0d0;
}
.button__yellow {
    color: #fff;
    background-color: #fad468;
    box-shadow: 0 5px #f3bc65;
}
.button__yellow:hover {
      box-shadow: 0 3px #f3bc65;
}
.dlist {
  margin-bottom: 20px;
}
.dlist__title {
    font-weight: 800;
    margin-bottom: 5px;
}
.dlist__body {
    margin-bottom: 10px;
    overflow-wrap: break-word;
}
* {
  box-sizing: border-box;
}
body, html {
  height: 100%;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0;
  background: #457fca;
  background: linear-gradient(to left, #457fca, #5691c8);
}
#app {
  height: 100%;
}



