
.urlform {
  width: 100%;
  margin-bottom: 40px;
}
.urlform__input {
    font: inherit;
    font-size: 1.2em;
    padding: 20px;
    width: 100%;
    border: 0;
    outline: 0;
    border-radius: 3px;
    box-shadow: 0 0 50px rgba(255, 255, 255, 0.1);
}
.notice {
  color: #fff;
  font-size: 1.1em;
  font-weight: 500;
}
.result {
  width: 100%;
  text-align: center;
}
.result__details {
    color: #fff;
    font-size: 1.4em;
}
.result__url {
    display: block;
    margin-bottom: 20px;
    color: #fff;
}
.result__original {
    opacity: 0.5;
    margin-bottom: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.result__original span {
      display: inline-block;
      max-width: 300px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      margin-right: 8px;
}
.result__stats {
    display: block;
    color: #fff;
    font-size: 0.9em;
    margin-bottom: 20px;
}
@media (min-width: 736px) {
.result {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.result__stats {
        margin-bottom: 0;
}
.result__details {
        margin-right: 60px;
}
}



